/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f9;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Container for Quantum Safe Practices Module */
.quantum-safe-practices-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Title and Description */
.module-title {
  font-size: 2rem;
  color: #2a71d0;
  text-align: center;
  margin-bottom: 1rem;
}

.module-description {
  text-align: center;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  color: #555;
}

.module-content {
  margin-bottom: 2rem;
  line-height: 1.6;
}

/* Quiz Section */
.quiz-section {
  padding: 1rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.quiz-title {
  font-size: 1.5rem;
  color: #2a71d0;
  margin-bottom: 1rem;
  text-align: center;
}

/* Question Styles */
.quiz-question {
  margin-bottom: 1.5rem;
}

.question-text {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #444;
}

/* Option Buttons */
.options-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.quiz-option {
  padding: 0.8rem 1.2rem;
  background: #2a71d0;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.quiz-option:hover {
  background-color: #1f5cb3;
  transform: translateY(-2px);
}

.quiz-option:active {
  background-color: #174991;
  transform: translateY(1px);
}

/* Completed Message */
.quiz-completed {
  text-align: center;
  font-size: 1.2rem;
  color: #4caf50;
  font-weight: bold;
}
